@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500&display=swap);
.app{display:grid;grid-auto-columns:1fr;grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"marginTop" "top" "top" "mid" "mid" "mid" "mid" "mid" "mid" "mid" "bot" "bot" "margin-bot";margin:0 auto;width:clamp(300px, 100%, 1000px);height:100%}.app__title{grid-area:top;font-size:50px;text-align:left;padding-left:15px;padding-top:15px;font-weight:500;color:#e6e6e6;font-family:"Roboto Mono", monospace}.app__middle{grid-area:mid;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.app__middle__text{font-size:30px;color:gray;font-family:"Roboto Mono", monospace}.Character{-webkit-flex-wrap:wrap;flex-wrap:wrap}.Character-current{background-color:rgba(255,255,255,0.1)}.Character-out{color:#ccc}*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;background-color:#333538;color:silver;-webkit-font-smoothing:antialiased}

